@red: #aa0000;
@green: #006600;
@gray: #CCC;
th {
  text-align: center;
}
td {
  padding: 5px 10px;
  cursor: default;
  user-select: none;
  min-width: 40px;
  text-align: center;
  &.calendar:hover {
    background-color: #ccc;
    cursor: pointer;
  }
  &.isset {
    background-color: #060;
    color: white;
    &:hover {
      background-color: #090;
    }
  }
  &.empty {
    background-color: #A00;
    color: white;
    &:hover {
      background-color: #F00;
    }
  }
}
.button {
  display: inline-flex;
  height: 34px;
  justify-content: center;
  align-items: center;
}

.modal {
  &.show {
    display: block;
    background-color: rgba(0, 0, 0, .4);
  }
  .modal-content {
    z-index: 999;
    .modal-header, .modal-footer {
      display: flex;
      justify-content: space-between;
    }
  }
  .close {
    border: 1px solid #ccc;
    display: inline-block;
    border-radius: 5px;
    width: 30px;
    height: 3 0px;
  }
}

.form-group {
  width: 100%;
  margin-bottom: 10px;
  .form-input {
    width: 100%;
    border: 1px solid @gray;
    padding: 5px;
    border-radius: 5px;
    &:focus {
      outline: none;
    }
  }
}

.un_payed {
  background-color: @red;
  color: white;
  padding: 5px 10px;
}
.payed {
  background-color: @green;
  color: white;
  padding: 5px 10px;
}
.weekend {
  color: @gray;
}