th {
  text-align: center;
}
td {
  padding: 5px 10px;
  cursor: default;
  user-select: none;
  min-width: 40px;
  text-align: center;
}
td.calendar:hover {
  background-color: #ccc;
  cursor: pointer;
}
td.isset {
  background-color: #060;
  color: white;
}
td.isset:hover {
  background-color: #090;
}
td.empty {
  background-color: #A00;
  color: white;
}
td.empty:hover {
  background-color: #F00;
}
.button {
  display: inline-flex;
  height: 34px;
  justify-content: center;
  align-items: center;
}
.modal.show {
  display: block;
  background-color: rgba(0, 0, 0, 0.4);
}
.modal .modal-content {
  z-index: 999;
}
.modal .modal-content .modal-header,
.modal .modal-content .modal-footer {
  display: flex;
  justify-content: space-between;
}
.modal .close {
  border: 1px solid #ccc;
  display: inline-block;
  border-radius: 5px;
  width: 30px;
  height: 3 0px;
}
.form-group {
  width: 100%;
  margin-bottom: 10px;
}
.form-group .form-input {
  width: 100%;
  border: 1px solid #CCC;
  padding: 5px;
  border-radius: 5px;
}
.form-group .form-input:focus {
  outline: none;
}
.un_payed {
  background-color: #aa0000;
  color: white;
  padding: 5px 10px;
}
.payed {
  background-color: #006600;
  color: white;
  padding: 5px 10px;
}
.weekend {
  color: #CCC;
}
